* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
  display: inline-block;
}
body {
  font-family: sans-serif;
}
.container {
  width: 1040px;
  margin: 0 auto;
}
.show {
  display: block;

}
.hide {
  display: none;
}
header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  z-index: 2;
  nav {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    select {
      height: 30px!important;
      width: 100px!important;
      margin-top: 30px;
    }
    button {
      height: 30px!important;
      width: 50px!important;
      margin-top: 30px;
      background-color: inherit;
      border: none;
    }
    ul {
      display: flex;
      li {
        padding: 35px 30px;
        a {
          font-style: normal;
          font-weight: 600;
          font-size: 16px;
          line-height: 28px;
          text-align: center;
          letter-spacing: 0.2px;
          color: #252B42;
          transition: all 0.5s;
        }
        a:hover {   
          color: #FA4A0C;
        }
      }
    }
  }
}
main {
  margin-top: 120px;
  .intro {
    background-image: url(./img/intro-big-bg.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: top center;
    .intro-wrapper {
      padding-top: 30px;
      h4 {
        font-style: normal;
        font-weight: bold;
        margin-top: 0px;
        font-size: 24px;
        line-height: 36px;
        text-align: center;
        letter-spacing: 0.1px;
        color: #F7F7F7;
      }
      h1 {
        font-style: normal;
        font-weight: bold;
        font-size: 58px;
        line-height: 80px;
        text-align: center;
        letter-spacing: 0.2px;
        color: #FFFFFF;

      }
      p {
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 38px;
        text-align: center;
        letter-spacing: 0.2px;
        color: #F7F7F7;
        position: relative;
      }
      a {
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 36px;
        position: absolute;
        margin-left: 35%;
        letter-spacing: 0.1px;
        margin-top: 3%;
        color: #FFFFFF;
        background: #FA4A0C;
        box-shadow: 0px 10px 30px rgba(183, 50, 39, 0.2);
        border-radius: 30px;
        padding: 10px 20px;
      }
      .link-1 {
        margin-left: 50%!important;
        border: 2px solid #fff!important;
        background-color: inherit!important;
        padding: 6px 20px;
      }
      img {
        margin-top: -20px;
        width: 550px;
        height: 640px;
        margin-left: 20%;
        border-bottom: 3px solid #E4E4E4;
      }
    }
  }
  .list-wrapper {
    h1 {
      font-weight: 600;
      font-size: 44px;
      line-height: 36px;
      letter-spacing: 0.1px;
      color: #000000;
      opacity: 0.8;
      text-align: center;
      margin-top: 10px;
      
    }
    ul {
      .teskari {
        flex-direction: row-reverse;
        margin-left: 150px!important;
      }
      li {
        display: flex;
        img {
          width: 500px;
          height: 620px;
        }
        div {
          margin-top: 200px;
          h4 {
            font-weight: bold;
          font-size: 24px;
          line-height: 36px;
          letter-spacing: 0.1px;
          color: #FA4A0C;
          }
          h2 {
            font-style: normal;
            font-weight: bold;
            font-size: 40px;
            line-height: 57px;
            width: 560px;
            letter-spacing: 0.2px;
            color: #252B42;
          }
          p {
            font-weight: 500;
            font-size: 24px;
            line-height: 38px;
            letter-spacing: 0.2px;
            color: #737373;
            width: 430px;
          }
        }
      }
    }
  }
  .end-section {
    background-image: url(./img/footer-bg.png);
    background-repeat: no-repeat;
    height: 350px;
    background-position: top center;
  }
  .end {
    h1 {
      font-weight: bold;
      font-size: 40px;
      line-height: 57px;
      padding-top: 100px;
      text-align: center;
      letter-spacing: 0.2px;
      color: #FFFFFF;
    }
    p {
      font-weight: 500;
      font-size: 24px;
      line-height: 38px;
      text-align: center;
      letter-spacing: 0.2px;
      padding: 10px 0;
      color: #FFFFFF;
    }
    a {
      font-weight: bold;
      font-size: 16px;
      line-height: 28px;
      letter-spacing: 0.2px;
      color: #FFFFFF;
      margin-left: 38%;
      margin-top: 20px;
      padding: 10px 20px;
      background: #FA4A0C;
      border-radius: 10px;
    }
    .link-2 {
      margin-left: 3%;
      padding: 6px 20px;
      background-color: inherit!important;
      border: 3px solid #fff;
    }
  }
}
.footer {
  display: flex;
  justify-content: space-around;
  ul {
    display: flex;
    padding: 45px 0;
  }
  a {
    padding: 45px 0;
  }
}
.theme-dark {
background-color: rgb(41, 40, 40)!important;
color: #fff!important;
  header {
    background-color: rgb(29, 29, 29)!important ;
    nav {
      button {
        i {
          color: blanchedalmond;
        }
      }
      ul {
        li {
          a {
            color: #fff;
          }
        }
      }
    }
  }
  .list-wrapper {
    h1 {
      color: #fff;
    }
    ul {
      li {
        div {
           h2, p {
            color: #fff!important;
          }
        }
      }
    }
  }
}
